body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  background-color: hsl(116, 65%, 98%);
  text-align: center;
  height: 100%;
}

.App-header {
  background-color: hsl(116, 65%, 78%);
  min-height: 20vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: black;
}

button {
  margin-left: 5px;
  padding: 2px;
}

.App-link {
  color: #61dafb;
}

.FileUpload p {
  margin-top: 30px;
  color: hsl(1, 1%, 20%);
}

.FileUpload li:hover {
  color: hsl(1, 50%, 20%);
}

.FileUpload li {
  cursor: pointer;
}

.FileUpload ul {
  list-style-type: none;
  text-align: center;
  margin: 0;
  margin-top: 5px;
}

.Footer {
  margin-top: 40px;
  opacity: .60;
  position: -webkit-sticky;
  position: sticky;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-size: calc(13px);
  color: black;
  bottom: 0px;
  min-height: 2vh;
  min-width: 100%;

}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

